import React, { Component } from 'react';
import PopUp from './popUp';

const valveInfo = [
      {
        id: 'K_HV001',
        title: 'HV-001',
        addClassName: { subText: '', mainText: 'valve' },
        panelId: 54,
        position: 'left center',
      },
      {
        id: 'K_HV004',
        title: 'HV-004',
        addClassName: { subText: 'hv-004', mainText: 'valve' },
        panelId: 56,
        position: 'left center',
      },
      {
        id: 'K_HV007',
        title: 'HV-007',
        addClassName: { subText: 'hv-007', mainText: 'valve' },
        panelId: 58,
        position: 'left center',
      },
    ];

export default function HandValves (props) {
 const handValves = valveInfo.map(element => (
      <div className="loop-container-hand-valve" key={element.id}>
        <PopUp
          addClassName={element.addClassName}
          title={element.title}
          id={element.id}
          points={props.points}
          panelId={element.panelId}
          position={element.position}
        />
      </div>
    ));
    return handValves;
}


