import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './redux/actions';
import diagram from '../../images/main-diagram-7.svg';
import HandValves from './handValves';
import AkPump from './AkPump';
import Temp from './Temp';
import Pump1 from './Pump1';
import Pump2 from './Pump2';

export class Loop extends Component {
  static propTypes = {
    loopContainer: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
  };

  constructor(props) {
    super(props);
    this.updatePoints = this.updatePoints.bind(this);
  }

  updatePoints() {
    this.props.actions.getToe();
    this.props.actions.getPump();
    this.props.actions.getPump2();
  }

  componentDidMount() {
    console.log('component');
    setInterval(this.updatePoints, 3000);
  }

  render() {
    const toePoints = this.props.loopContainer.toePoints;

    return (
      <div className="loop-container-loop">
        <div className="loop-container">
          <div className="loop-wrapper">
            <Pump1 />

            <Pump2 />

            <div className="ak-p-attrib">
              <AkPump />
            </div>

            <HandValves points={toePoints} />

            <div className="place-wrapper loop-1">
              <div className="main-text-div loop-icon">
                <div className="main-text">
                  <strong>1</strong>
                </div>
              </div>
            </div>
            <div className="place-wrapper loop-1 loop-3">
              <div className="main-text-div loop-icon">
                <div className="main-text">
                  <strong>3</strong>
                </div>
              </div>
            </div>
            <div className="place-wrapper loop-1 loop-2">
              <div className="main-text-div loop-icon">
                <div className="main-text">
                  <strong>2</strong>
                </div>
              </div>
            </div>

            <Temp />

            <div className="div-block">
              <img src={diagram} className="diagram" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    loopContainer: state.loopContainer,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Loop);



