import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './redux/actions';
import PopUp from './popUp';

export class Pump1 extends Component {
  static propTypes = {
    loopContainer: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
  };

  render() {
    const points = this.props.loopContainer.pumpPoints;
    return (
      <div className="loop-container-pump-1">
        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-rpm',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P1_RPM"
          id="fSpeed"
          points={points}
          panelId={22}
          position="right center"
        />

        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-fe',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P1_FE"
          id="fVolumeFlow"
          points={points}
          panelId={20}
          position="right center"
        />

        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-pe',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P1_PE"
          id="fHead"
          points={points}
          panelId={18}
          position="right center"
        />

        <PopUp
          addClassName={{ subText: 'temp ak-p-qq p1-2', mainText: 'temp', subTextDiv: 'p1-p2-sub' }}
          title="P1_PE"
          id="fPumpLiquidTemp"
          points={points}
          panelId={62}
          position="right center"
        />
      </div>
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    loopContainer: state.loopContainer,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Pump1);



