import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './redux/actions';
import Popup from 'reactjs-popup';
import PopUp from './popUp';

export class Pump2 extends Component {
  static propTypes = {
    loopContainer: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
  };

  render() {
    const points = this.props.loopContainer.pump2Points;
    return (
      <div className="loop-container-pump-2">
        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-rpm p2',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P2_RPM"
          id="fSpeed"
          points={points}
          panelId={24}
          position="right center"
        />

        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-fe p2',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P2_FE"
          id="fVolumeFlow"
          points={points}
          panelId={26}
          position="right center"
        />

        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p1-pe p2',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P2_PE"
          id="fHead"
          points={points}
          panelId={28}
          position="right center"
        />

        <PopUp
          addClassName={{
            subText: 'temp ak-p-qq p1-2 p2',
            mainText: 'temp',
            subTextDiv: 'p1-p2-sub',
          }}
          title="P2_TE"
          id="fPumpLiquidTemp"
          points={points}
          panelId={64}
          position="right center"
        />
      </div>
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    loopContainer: state.loopContainer,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Pump2);



